:root{
  --background: #2c2b38;
  --background_dark : #22212F;

  --main1-hue : 35;
  --main1-lightness: 45%;
  --main1-saturation: 100%;

  --cards : #ffffff;
  --main1 : hsl(var(--main1-hue), var(--main1-saturation), var(--main1-lightness));
  --main1-dark : hsl(var(--main1-hue), var(--main1-saturation), calc(var(--main1-lightness) - 8%));
  --main1-light : hsl(calc(var(--main1-hue) - 5), var(--main1-saturation), calc(var(--main1-lightness) + 8%));
  --main1_transparent : hsla(var(--main1-hue), calc(var(--main1-saturation) - 20%), var(--main1-lightness), 0.1);

  --positive_value: #1EEE58;
  --negative_value: #ff0000;
  --paragraph_color: #B6ACAC;
  --title_color: white;

  --light_grey: rgb(197, 197, 197);
  --medium_grey: rgb(139, 139, 139);

  font-family: 'Inter', sans-serif;
  
}



/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* width */
::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #ffffff1a;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: rgba(131, 131, 131, 0.425);
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: rgba(131, 131, 131, 0.425);
}

html{
  background-color: var(--background);
}

.App{
  padding: 30px 30px 30px 410px;
  height: calc(100vh - 60px);
}

.left_panel{
  position: absolute;
  top: 0;
  left: 0;
  width: 350px;
  height: 100vh;
  background-color: var(--background_dark);
  box-shadow: #000000c5 0 0 10px 0;
  padding: 0px 15px 0 15px;
  row-gap: 15px;
  overflow-y: scroll;
}

.header_controls{
  padding: 10px 0;
  width: 100%;
  display: flex;
  justify-content: right;
}

.left_panel_add_button{
  height: 30px;
  width: 30px;
  background-color: var(--background);
  box-shadow: #00000065 0 0 4px 0;
  cursor: pointer;
}
.left_panel_add_button svg{
  width: 100%;
  height: 100%;
}

.left_panel .revenu_container{
  position: relative;
  background-color: var(--background);
  box-shadow: #00000065 0 0 5px 0;
  padding: 10px 15px 15px 15px;
  border-radius: 3px;
  margin-bottom: 10px;
}
.revenu_title{
  color: var(--title_color);
  letter-spacing: 1px;
  font-size: 16px;
  font-weight: bold;
  cursor: default;
  letter-spacing: 1px;
}
.revenu_value{
  font-weight: bold;
  cursor: default;
}

.positive_value{
  color: var(--positive_value);
}
.negative_value{
  color: var(--negative_value);
}
.paragraph_color{
  color: var(--paragraph_color);
}
.revenu_repetitions{
  position: relative;
  margin: 10px 0 5px 0;
  color: var(--paragraph_color);
  font-size: 14px;
  padding-right: 35px;
  cursor: default;
  letter-spacing: 1px;
}
.revenu_settings{
  display: flex;
  flex-direction: column;
  row-gap: 15px;
}
.revenu_settings hr{
  margin-top: 15px;
  width: 100%;
  border: 1px solid rgba(119, 119, 119, 0.459);
}
.revenu_setting_icon{
  position: absolute;
  top: 0;
  right: 0;
  width: 22px;
  height: 22px;
  cursor: pointer;
}
.revenu_setting_icon svg{
  width: 100%;
  height: 100%;
}


.repeated_event_headers{
  color: var(--title_color);
  letter-spacing: 1px;
  font-weight: bold;
  font-size: 14px;
}

.time_picker{
  width: 100%;
  background-color: var(--light_grey);
  border-radius: 3px;
  padding: 5px 0;
}
.time-selectors select{
  background-color: #00000000;
}

.interval_picker{
  background-color: var(--background);
  padding: 5px 0;
  border-radius: 3px;
  margin: 4px;
  box-shadow: black 0 0 4px 0;
}
.interval-selectors input{
  background-color: var(--light_grey);
  color: white;
}
.interval-selectors input::placeholder{
  color: black;
}
.interval-selectors select{
  background-color: var(--light_grey);
  color: white;
}
select option {
  background: var(--background);
  color: #fff;
}

.revenu_header_line .modifiable_value{
  font-weight: bold;
  letter-spacing: 2px;
}
.modifiable_value::placeholder{
  color: var(--medium_grey);
}
.modifiable_value:focus{
  outline: none;
}

.revenu_inline_setting_text{
  font-size: 15px;
  color: var(--paragraph_color);
  width: 270px;
  line-height: 24px;
}
.revenu_inline_setting_icon{
  height: 24px;
}
.revenu_inline_setting_icon svg{
  height: 100%;
  width: 100%;
}


.rdp{
  --rdp-cell-size: 42px !important;
  --rdp-caption-font-size: 18px !important;
  --rdp-accent-color: var(--main1) !important;
  --rdp-background-color: var(--main1_transparent) !important;
  --rdp-accent-color-dark: var(--main1-hover) !important;
  --rdp-background-color-dark: #180270 !important;
  --rdp-outline: 2px solid var(--rdp-accent-color) !important;
  --rdp-outline-selected: 3px solid var(--rdp-accent-color) !important;
  background-color: var(--background);
  border-radius: 5px;
  padding: 0.5em;
  margin: 4px !important;
  box-shadow: black 0 0 4px 0;
  width: auto !important;
}
.rdp-day_today:not(.rdp-day_outside):not(.rdp-day_selected) {
  font-weight: bold;
  color: var(--main1);
}
.rdp-dropdown{
  padding: 5px !important;
}
.rdp-month.rdp-caption_start.rdp-caption_end{
  width: 100%;
}
.rdp-head_cell{
  color: var(--main1-dark);
}
.rdp-cell{
  color: white;
}

.context_menu{
  background-color: var(--background);
  box-shadow: black 0 0 4px 0;
  border-radius: 4px;
  padding: 10px;
  row-gap: 0px;
  color: white;
}
.context_menu_icon{
  height: 20px;
  width: 20px;
}
.context_menu_icon svg{
  height: 100%;
  width: 100%;
}
.context_menu_button{
  padding: 8px 15px 8px 5px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
.context_menu_button:hover{
  background-color: rgba(0, 0, 0, 0.2);
}
.context_menu_button span{
  line-height: 20px;
  color: white;
}

.on_click_menu{
  top: 0px;
  width: 320px;
  padding: 10px;
  border-radius: 4px;
}

.on_click_menu.visible{
  background-color: var(--background_dark);
  box-shadow: black 0 0 4px 0;
  opacity: 1;
  visibility: visible;
}

.on_click_menu_pointer{
  background: linear-gradient(225deg, rgba(79,3,3,0) 0%, rgba(142,1,1,0) 50%, var(--background_dark) 50%, var(--background_dark) 100%);
  box-shadow: rgba(0, 0, 0, 0.4) -2px 2px 2px 0;
}

.graphs_container{
  border: 2px dashed black;
  padding: 10px;
  height: 70%;
}

