
.context_menu{
    position: fixed;
    display: flex;
    flex-direction: column;
    margin: 0;
    visibility: hidden;
    opacity: 0;
    transition: opacity 0.3s;
}
.context_menu.active{
    visibility: visible;
    opacity: 1;
}

.context_menu hr{
    width: 80%;
}

.context_menu_button{
    display: inline-flex;
    column-gap: 10px;
}