.on_click_menu{
    position: fixed;
    display: flex;
    flex-direction: column;
    row-gap: 10px;
    opacity: 0;
    visibility: hidden;
    transition: 0.2s;
    margin-left: -20px;
    min-height: 70px;
    min-width: 70px;
}

.on_click_menu.visible{
    opacity: 1;
    visibility: visible;
    margin-left: 0;
}

.on_click_menu_pointer{
    position: fixed;
    left: 0;
    height: 30px;
    width: 30px;
    transform: translate(-50%, 0%) rotate(45deg);
    margin-left: -20px;
    transition: 0.2s;
}

.on_click_menu.visible .on_click_menu_pointer{
    margin-left: 0;
}